  font-weight: 700;
  line-height: 0.7222222222rem;
}
.primary:hover {
  background: #fff;
  transform: translateY(-0.0555555556rem);
}

.outline {
  background: transparent;
  border: 0.1111111111rem solid #ffcc00;
  padding: 0.5555555556rem 1.2222222222rem;
  border-radius: 1.6666666667rem;
  font-size: 0.7222222222rem;
  font-weight: 700;
  line-height: 0.7222222222rem;
  cursor: pointer;
}
.outline:hover {
  background: #ffcc00;
  transform: translateY(-0.0555555556rem);
}

.call {
  display: inline-flex;
  align-items: center;
  gap: 0.4444444444rem;
  border: 0.1111111111rem solid #ffcc00;
  padding-left: 1.1111111111rem;
  padding-right: 0.2222222222rem;
  padding-top: 0.1666666667rem;
  padding-bottom: 0.1666666667rem;
  border-radius: 1.6666666667rem;
  text-decoration: none;
  color: #000;
  width: fit-content;
  font-size: 1.0555555556rem;
  line-height: 1.1111111111rem;
  font-weight: 900;
  transition: all 0.3s ease;
}
.call strong {
  display: flex;
  gap: 0.5555555556rem;
}
.call img {
  background-color: #ffcc00;
  padding: 0.2777777778rem;
  border-radius: 50%;
  max-width: 1.6666666667rem;
  transition: background-color 0.3s ease;
}
.call:hover {
  background: #ffcc00;
  transform: translateY(-0.1111111111rem);
  box-shadow: 0 0.4444444444rem 1.1111111111rem rgba(255, 204, 0, 0.35);
}
.call:hover img {
  background-color: #fff;
}

.business-btn {
  font-size: 0.7222222222rem;
  font-weight: 700;
  line-height: normal;
  padding-bottom: 0.5555555556rem;
  display: flex;
  gap: 0.2777777778rem;
  padding: 0;
}
.business-btn img {
  max-width: 0.8888888889rem;
  transition: transform 0.3s ease;
}
.business-btn:hover {
  text-decoration: underline;
}
.business-btn:hover img {
